@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300&display=swap);
:root {
  --font-color: rgba(0,0,0,.48);
}

.artists{display:flex;justify-content:center}
.header {
  width: 100%;
  padding: 40px 90px;
}

.header__title-container {
  display: flex;
  justify-content: space-between;
  color: var(--font-color);
}

.header__title-container > p {
  margin: 0;
}

.header__input_search {
  border: none;
  outline: none;
}

.header__input_search::-webkit-input-placeholder {
  color: var(--font-color);
  font-size: 15px;
  font-weight: 100;
}

.header__input_search:-ms-input-placeholder {
  color: var(--font-color);
  font-size: 15px;
  font-weight: 100;
}

.header__input_search::placeholder {
  color: var(--font-color);
  font-size: 15px;
  font-weight: 100;
}

.header__navigation-container {
  padding-top: 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  overflow: hidden;
}

.header__navigation-links_right,
.header__navigation_social-links {
  justify-content: flex-end;
}

.header__logo {
  height: 100px;
}

.header__navigation {
  display: flex;
  flex-wrap: wrap;
  width: 390px;
  transition: color 570ms ease-in-out;
}

.header__navigation:hover a,
.header__navigation:hover {
  fill:rgba(0, 0, 0, 0.3);
  color: rgba(0, 0, 0, 0.3);
  transition: color 570ms ease-in-out, fill 570ms ease-in-out;
}

.header__navigation_right {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100px;
}

.header__navigation li:hover,
.header__navigation a:hover {
  cursor: pointer;
  color: black;
  fill:black;
  transition: color 0.1s ease-in, fill 0.1s ease-in;
}

.header__navigation li,
.header__navigation a:hover {
  font-size: 14px;
  white-space: nowrap;
  margin-top: 10px;
}

.header__navigation-links_left > li {
  margin-right: 15px;
}

.header__navigation-links_right > li {
  margin-left: 15px;
}

/* .header__navigation-links_right > li:first-child,
.header__navigation_social-links > li:first-child {
  margin-left: 0;
} */



.social-links {
  display: flex;
}

.social-links svg {
  width: 30px;
  height: 30px;
}

.social-links > li {
  text-decoration: none;
}
.main {
  background-color: #fff;
  max-width: 1680px;
  width: 100vw;
  height: 100vh;
  display: block;
  margin: auto;
  font-family: Raleway, Arial, Helvetica, sans-serif;
  font-style: normal;
  letter-spacing: 0.15em;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

ul {
  list-style-type: none;
  margin: 0;
  -webkit-padding-start: 0;
          padding-inline-start: 0;
}

li {
  display: block;
}

a {
  text-decoration: none;
  color: black;
}

h2, h3 {
  font-size: 20px;
  letter-spacing: 3px;
  font-weight: 400;
}

p {
  font-weight: 300;
  font-style: normal;
  font-size: 15px;
  letter-spacing: .1em;
}
@font-face {
  font-family: 'Raleway';
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src: url(/arts/static/media/Raleway-Regular.a9338128.ttf) format('ttf');
}
.footer{padding:50px 0;width:90%;margin:auto}.footer ul{display:flex}.footer__container{width:90%;margin:auto}.footer__container a{color:#000;font-weight:700}.footer__container li{border-bottom:1px solid rgba(0,0,0,.315);height:15px}.footer__container p{white-space:pre-wrap;margin:17px 0}.footer__container ul>p{margin:0}
.social{padding:150px 0;border-bottom:1px solid rgba(0,0,0,.8)}.social__container{width:66.6%;margin:auto;display:flex;flex-direction:column;align-items:center}.social__container>ul{display:flex}.social__container>ul p{white-space:pre;margin:0}.social__container>ul>li{border-bottom:rgba(0,0,0,.4) 1px solid;margin-bottom:10px}.social__container>ul>li a{color:rgba(0,0,0,.63)}.social__container>ul>li a:hover{fill:rgba(0,0,0,.3);color:rgba(0,0,0,.3);transition:color 570ms ease-in-out,fill 570ms ease-in-out}

.button {
  padding: 0;
  background-color: transparent;
  outline: 2px solid #1d1d1d;
  border: none;
  letter-spacing: .1em;
  margin: 17px 0;
  transition: background-color 100ms linear, color 100ms linear;
}

.button:hover,
.button:hover a:visited,
.button:hover a:visited {
  background-color: #1d1d1d;
  color: #fff;
}

.button a:visited,
.button a {
  padding: 13px 26px;
  display: block;
  height: 100%;
  width: 100%;
  text-decoration: none;
  color: #1d1d1d;
}

